*{
    margin: 10px;
    overflow: hidden;
    margin-left: 10%;
}

#in{
   height: 10%;
   font-size: 100%;
   width: auto;
   margin: 5px;
   margin-right: 5px;
   margin-bottom: 20px;
   border: 0.1px solid cyan;
   color: cyan;
   background-color: black;
   text-align: center;
}


h3{
    margin: 25px;
    color: cyan;
    width: auto;
}
body{
    background-color:black;
    display: grid;
    place-items: center;
    grid-template-columns: 89%;
    
}

#ino:hover{
    /*background-color: cyan;*/
    box-shadow: 2px 2px 30px cyan;
    filter: opacity(95%);
    transition: 0.5s;
}

#ino{
    background-color: black;
    border: 2px solid cyan;
    height: auto;
    width: auto;
    
    border-radius: 20px;
    margin-top: 20%;
    
}

#ind{
    width: auto;
    border: 2px solid cyan;
    height: 300px;
   
    display: grid;
    place-items: center;
    border-radius: 20px;
    display: grid;
    place-items: center;
}

img{
    color: cyan;
}

